"use client"

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Badge } from '@/components/ui/badge'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Switch } from '@/components/ui/switch'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'

interface Employee {
  id: string
  name: string
  email: string
  status: 'active' | 'idle' | 'offline'
  timeToday: number
  lastActivity: Date
  screenshotCount: number
}

export default function AdminPanel() {
  const [screenshotInterval, setScreenshotInterval] = useState(30)
  const [idleThreshold, setIdleThreshold] = useState(300)
  const [trackingEnabled, setTrackingEnabled] = useState(true)
  const [employees, setEmployees] = useState<Employee[]>([
    {
      id: '1',
      name: 'John Doe',
      email: 'john@company.com',
      status: 'active',
      timeToday: 25200, // 7 hours in seconds
      lastActivity: new Date(Date.now() - 5 * 60 * 1000), // 5 minutes ago
      screenshotCount: 240
    },
    {
      id: '2',
      name: 'Jane Smith',
      email: 'jane@company.com',
      status: 'idle',
      timeToday: 18000, // 5 hours in seconds
      lastActivity: new Date(Date.now() - 15 * 60 * 1000), // 15 minutes ago
      screenshotCount: 180
    },
    {
      id: '3',
      name: 'Mike Johnson',
      email: 'mike@company.com',
      status: 'offline',
      timeToday: 28800, // 8 hours in seconds
      lastActivity: new Date(Date.now() - 2 * 60 * 60 * 1000), // 2 hours ago
      screenshotCount: 320
    },
    {
      id: '4',
      name: 'Sarah Wilson',
      email: 'sarah@company.com',
      status: 'active',
      timeToday: 21600, // 6 hours in seconds
      lastActivity: new Date(Date.now() - 2 * 60 * 1000), // 2 minutes ago
      screenshotCount: 216
    }
  ])

  const formatTime = (seconds: number) => {
    const hours = Math.floor(seconds / 3600)
    const minutes = Math.floor((seconds % 3600) / 60)
    return `${hours}h ${minutes}m`
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'active': return 'bg-green-500'
      case 'idle': return 'bg-yellow-500'
      case 'offline': return 'bg-gray-500'
      default: return 'bg-gray-500'
    }
  }

  const handleSaveSettings = () => {
    // In a real app, this would save to backend
    alert('Settings saved successfully!')
  }

  const totalActiveEmployees = employees.filter(emp => emp.status === 'active').length
  const totalTimeToday = employees.reduce((sum, emp) => sum + emp.timeToday, 0)
  const averageTimePerEmployee = totalTimeToday / employees.length

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="flex justify-between items-center mb-8">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Admin Control Panel</h1>
            <p className="text-gray-600">Manage employee tracking settings and monitor activity</p>
          </div>
          <div className="flex gap-4">
            <Button 
              variant="outline" 
              onClick={() => window.open('/admin/reports', '_blank')}
            >
              View Reports
            </Button>
            <Button 
              variant="outline" 
              onClick={() => window.open('/', '_blank')}
            >
              Employee Dashboard
            </Button>
          </div>
        </div>

        {/* Overview Stats */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium">Active Employees</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-green-600">{totalActiveEmployees}</div>
              <p className="text-xs text-gray-600">Currently tracking</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium">Total Employees</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{employees.length}</div>
              <p className="text-xs text-gray-600">Registered users</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium">Total Time Today</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{formatTime(totalTimeToday)}</div>
              <p className="text-xs text-gray-600">All employees combined</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium">Average Time</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{formatTime(averageTimePerEmployee)}</div>
              <p className="text-xs text-gray-600">Per employee today</p>
            </CardContent>
          </Card>
        </div>

        <Tabs defaultValue="employees" className="space-y-6">
          <TabsList>
            <TabsTrigger value="employees">Employee Management</TabsTrigger>
            <TabsTrigger value="settings">Tracking Settings</TabsTrigger>
          </TabsList>

          <TabsContent value="employees">
            <Card>
              <CardHeader>
                <CardTitle>Employee Activity</CardTitle>
              </CardHeader>
              <CardContent>
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Employee</TableHead>
                      <TableHead>Status</TableHead>
                      <TableHead>Time Today</TableHead>
                      <TableHead>Last Activity</TableHead>
                      <TableHead>Screenshots</TableHead>
                      <TableHead>Actions</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {employees.map((employee) => (
                      <TableRow key={employee.id}>
                        <TableCell>
                          <div>
                            <div className="font-medium">{employee.name}</div>
                            <div className="text-sm text-gray-600">{employee.email}</div>
                          </div>
                        </TableCell>
                        <TableCell>
                          <Badge className={`${getStatusColor(employee.status)} text-white`}>
                            {employee.status}
                          </Badge>
                        </TableCell>
                        <TableCell className="font-medium">
                          {formatTime(employee.timeToday)}
                        </TableCell>
                        <TableCell className="text-sm text-gray-600">
                          {employee.lastActivity.toLocaleString()}
                        </TableCell>
                        <TableCell>{employee.screenshotCount}</TableCell>
                        <TableCell>
                          <Button variant="outline" size="sm">
                            View Details
                          </Button>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="settings">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <Card>
                <CardHeader>
                  <CardTitle>Screenshot Settings</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div>
                    <Label htmlFor="screenshot-interval">Screenshot Interval (seconds)</Label>
                    <Input
                      id="screenshot-interval"
                      type="number"
                      value={screenshotInterval}
                      onChange={(e) => setScreenshotInterval(Number(e.target.value))}
                      min="10"
                      max="300"
                    />
                    <p className="text-xs text-gray-600 mt-1">
                      How often to take screenshots (10-300 seconds)
                    </p>
                  </div>

                  <div>
                    <Label htmlFor="idle-threshold">Idle Threshold (seconds)</Label>
                    <Input
                      id="idle-threshold"
                      type="number"
                      value={idleThreshold}
                      onChange={(e) => setIdleThreshold(Number(e.target.value))}
                      min="60"
                      max="1800"
                    />
                    <p className="text-xs text-gray-600 mt-1">
                      Time before marking employee as idle (60-1800 seconds)
                    </p>
                  </div>

                  <div className="flex items-center space-x-2">
                    <Switch
                      id="tracking-enabled"
                      checked={trackingEnabled}
                      onCheckedChange={setTrackingEnabled}
                    />
                    <Label htmlFor="tracking-enabled">Enable Time Tracking</Label>
                  </div>
                </CardContent>
              </Card>

              <Card>
                <CardHeader>
                  <CardTitle>Current Settings</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="space-y-2">
                    <div className="flex justify-between">
                      <span className="text-sm font-medium">Screenshot Interval:</span>
                      <span className="text-sm">{screenshotInterval} seconds</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-sm font-medium">Idle Threshold:</span>
                      <span className="text-sm">{idleThreshold} seconds</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-sm font-medium">Tracking Status:</span>
                      <Badge variant={trackingEnabled ? "default" : "secondary"}>
                        {trackingEnabled ? "Enabled" : "Disabled"}
                      </Badge>
                    </div>
                  </div>

                  <Button onClick={handleSaveSettings} className="w-full">
                    Save Settings
                  </Button>
                </CardContent>
              </Card>
            </div>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  )
}
